.section-venue {
  position: relative;

  .container {
    max-width: 1080px;
    margin: 0 auto;
  }

  .row {
    position: relative;
  }

  .bg {
    background: rgb(10, 9, 38);
    background: linear-gradient(180deg, rgba(10, 9, 38, 1) 0%, rgba(23, 12, 68, 1) 100%);
  }

  .address {
    color: $color-brand-secondary;
    line-height: 35px;
  }

  .stars {
    height: calc(668px - 100px);
    background-image: url(/dist/img/venue-stars.svg);
  }

  .h2 {
    @include md-up-to {
      line-height: 1.5;
    }
  }
}
